<template>
    <div id="edit">
        <el-row>
            <el-col :span="24">
                <el-card shadow="always">
                    <div class="title"><span class="iconfont icon-icon-up"></span>{{title}}</div>
                    <el-form :model="edit">
                        <div class="sjgh">
                            <div class="sj"><span class="left">上级规划：</span><p class="right">{{edit.sj.mc}}</p></div>
                            <div class="sj"><span class="left">上级领导：</span><p class="right">{{edit.leader}}</p></div>
                        </div>
                        <el-form-item label="规划名称：">
                            <el-input v-model="edit.mc" ref="ghmc" type="textarea" resize="none" autosize placeholder="请输入规划名称"></el-input>
                        </el-form-item>
                        <div class="form_item">
                            <el-form-item label="负责人：" class="fzr">
                                <el-col :span="12" style="width:100%;padding:0;">
                                    <el-autocomplete
                                    class="inline-input"
                                    v-model="edit.fzr"
                                    :fetch-suggestions="queSearch"
                                    placeholder="请选择负责人"
                                    @select="fzr"
                                    clearable>
                                    </el-autocomplete>
                                </el-col>
                            </el-form-item>
                            <el-form-item label="经费预算：" class="jfys">
                                <el-input v-model="edit.jfys" ref="jf" type="number" placeholder="请输入金额" autocomplete="off">
                                    <template slot="append">万元</template>
                                </el-input>
                            </el-form-item>
                            <el-form-item label="紧急程度：" class="important">
                                <el-select v-model="edit.important" @change="jjcd" placeholder="请选择项目重要程度" style="width:100%;">
                                    <el-option
                                    v-for="item in option2"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="分解权重：" class="quanzhong">
                                <el-input v-model="input" ref="qz" placeholder="请输入权重大小" onkeyup="this.value=this.value.replace(/[^\-?\d.]/g,'')"></el-input>
                            </el-form-item>
                        </div>
                        <div class="form_item">
                            <el-form-item label="开始时间：" class="starrttime">
                                <el-date-picker
                                    v-model="edit.start"
                                    type="date"
                                    placeholder="选择开始日期">
                                </el-date-picker>
                            </el-form-item>
                            <el-form-item label="结束时间：" class="endtime">
                                <el-date-picker
                                    v-model="edit.end"
                                    type="date"
                                    placeholder="选择结束日期">
                                </el-date-picker>
                            </el-form-item>
                        </div>
                        <el-form-item label="参与人：">
                            <el-select v-model="edit.cyr" @change="cyz" multiple filterable="" default-first-option placeholder="请选择参与人" style="width:100%;">
                                <el-option v-for="item in options" :key="item.user_id" :label="item.name" :value="item.user_id" :disabled="item.name===edit.fzr?true:false">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <div class="khbz">
                            <el-form-item label="考核指标：" style="width:33%;margin-right:35px;">
                                <el-col :span="12" style="width:100%;padding:0;">
                                    <el-autocomplete
                                    class="inline-input"
                                    v-model="edit.zgzb"
                                    :fetch-suggestions="querySearch"
                                    placeholder="请输入内容"
                                    @select="handleSelect"
                                    clearable>
                                    </el-autocomplete>
                                </el-col>
                            </el-form-item>
                            <el-form-item label="诊改目标：" style="flex:1;">
                                <div class="zhengaimubiao">
                                    <el-select v-model="edit.mark" @change="fh" class="mb_mark" placeholder="请选择">
                                        <el-option
                                        v-for="item in option1"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                        </el-option>
                                    </el-select>
                                    <el-input class="mb_ipt1" ref="mb" v-model="edit.zgmb" placeholder="请输入目标" autocomplete="off"></el-input>
                                </div>
                            </el-form-item>
                        </div>
                        <el-form-item label="规划简介：">
                            <el-input v-model="edit.jj" ref="xmjj" type="textarea" :autosize="{ minRows: 6}" placeholder="请输入简介内容"></el-input>
                        </el-form-item>
                    </el-form>
                    <div class="edit-footer">
                        <el-button type="primary" @click="keep()">保 存</el-button>
                        <el-button @click="cancel()">返 回</el-button>
                    </div>
                </el-card>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import store from '../store/store.js'
export default {
    name: 'edit',
    inject:["reload"],
    data() {
        return {
            input:"",
            title: '编辑诊改工作规划',
            edit: {
                title: '规划项目编辑',
                level: '4',
                id: '1',
                mc:'',
                important: '',
                quanzhong: '5',
                leader: '李老师',
                sj: {gh: '上级规划',mc: ''},
                start: '',
                end: '',
                jfys: 2.20,
                fzr: '',
                fid:'',
                cyr: [],
                zgzb: '',
                zgzbID: '',
                mark: '1',
                zgmb:'10000人',
                jd: 30,
                jj: '',
                dj: '',
            },
            rules:{
                khzb: [
                    { required: true, message: '请输入考核指标', trigger: 'change' }
                ],
                start: [
                    { required: true, message: '请选择开始时间', trigger: 'change' }
                ],
                end: [
                    { required: true, message: '请选择结束时间', trigger: 'change' }
                ],
            },
            options101:[],
            restaurants:[],
            restaurants1:[],
            options:[],
            option1: [{
                value: '1',
                label: '＞'
            },{
                value: '2',
                label: '≥'
            },{
                value: '3',
                label: '＜'
            },{
                value: '4',
                label: '≤'
            },{
                value: '5',
                label: '＝'
            },{
                value: '6',
                label: '≠'
            }],
            option2: [
                {
                    value: '1',
                    label: '重要'
                },{
                    value: '2',
                    label: '普通'
                },{
                    value: '3',
                    label: '暂缓'
                }
            ],
            option3: [
                {
                    value: '0',
                    label: '0'
                },
                {
                    value: '1',
                    label: '1'
                },
                {
                    value: '2',
                    label: '2'
                },
                {
                    value: '3',
                    label: '3'
                },
                {
                    value: '4',
                    label: '4'
                },
                {
                    value: '5',
                    label: '5'
                },
                {
                    value: '6',
                    label: '6'
                },
                {
                    value: '7',
                    label: '7'
                },
                {
                    value: '8',
                    label: '8'
                },
                {
                    value: '9',
                    label: '9'
                },
            ]
        }
    },
    methods: {
        querySearch(queryString, cb) {
            var restaurants = this.restaurants;
            var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
            // 调用 callback 返回建议列表的数据
            cb(results);
        },
        queSearch(queryString, cb) {
            var restaurants1 = this.restaurants1;
            var results = queryString ? restaurants1.filter(this.createFilter(queryString)) : restaurants1;
            // 调用 callback 返回建议列表的数据
            cb(results);
        },
        fzr(e){
            localStorage.setItem("fid",e)
            this.edit.fid=e.id;
            console.log(e.id)
        },
        jjcd(value){
            localStorage.setItem("jjcd",value)
        },
        kssj(value){
            console.log(value)
            const newDate1 = value.getFullYear() + '-' + (value.getMonth() + 1) + '-' + value.getDate();
            console.log(newDate1)
            localStorage.setItem("newDate1",newDate1)
        },
        jssj(value){
            console.log(value)
            const newDate2 = value.getFullYear() + '-' + (value.getMonth() + 1) + '-' + value.getDate();
            console.log(newDate2)
            localStorage.setItem("newDate2",newDate2)
        },
        fh(value){
            localStorage.setItem("fh",value)
        },
        cyz(e){
            localStorage.setItem("cyz",e)
        },
        createFilter(queryString) {
                return (restaurant) => {
                return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
            };
        },
        loadAll() {
            return [
                { "value": "就业率" ,
                "label":"1"},
                { "value": "双师型比例",
                "label":"2" },
                { "value": "新生增长率" ,
                "label":"3"},
            ];
        },
        handleSelect(item) {
            this.edit.zgzbID = item.id
            console.log('kankan'+item);
        },
        keep(){
            this.$confirm('确认保存吗？')
               .then(_ => {
                    // 向后台发起请求将this.edit的值发送过去
                if(this.edit.start && this.edit.end){
                var d_start = new Date(this.edit.start)
                var d_end = new Date(this.edit.end)
                var s_m = d_start.getMonth()
                var s_d = d_start.getDate()
                var e_m = d_end.getMonth()
                var e_d = d_end.getDate()
                console.log(s_m,s_d)
                if(s_m + 1 < 10){
                    s_m = "0" + (s_m + 1)
                }else{
                    s_m = s_m + 1
                }
                if(s_d < 10){
                    s_d = "0" + s_d
                }
                if(e_m + 1 < 10){
                    e_m = "0" + (e_m + 1)
                }else{
                    e_m = e_m + 1
                }
                if(e_d < 10){
                    e_d = "0" + e_d
                }
                this.edit.start = d_start.getFullYear() + '-' + s_m + '-' + s_d
                this.edit.end = d_end.getFullYear() + '-' + e_m + '-' + e_d
                
            }
            var xfid = this.$route.query.fid;
            var fdj = this.$route.query.fdj;
            var that = this;
            var cyr = this.edit.cyr.toString();
            console.log(cyr)
            var id = localStorage.getItem("id3");
            var ghmc = this.$refs.ghmc.value;
            var fid = this.edit.fid;
            var jf = this.$refs.jf.value;
            var jjcd = this.edit.important;
            var qz = this.$refs.qz.value;
            var xmjj = this.$refs.xmjj.value;
            var khbz = this.edit.zgzbID;
            console.log('asdasdsaf'+khbz)
            var fh = this.edit.mark;
            var mb = this.$refs.mb.value;
            var newDate1 = this.edit.start;
            var newDate2 = this.edit.end;
            $.ajax({
                type: "post",
                url: this.baseUrl+"/ydjxzg/mb/editTarget.action",
                data: {
                    userlist:cyr,//参与人
                    Id:id,//项目id
                    target_name:ghmc,//项目名
                    manager_id:fid,//负责人ID
                    Funds:jf,//项目经费
                    Importancedj:jjcd,  //重要程度
                    Weight:qz,  //权重
                    start_time:newDate1,  //开始时间
                    end_time:newDate2,  //结束时间
                    target_info:xmjj, //项目描述
                    target_stand:khbz, //考核标准
                    target_stand_contion:fh, //1 大于等于 2大于 3等于 4小于等于 5小于 6不等于
                    target_stand_info:mb //考核标准描述
                },
                dataType: "json",
                success: function (res) {
                    console.log(that.edit)
                    that.$message.success('保存成功！')
                    // that.$router.push({path: "/tree?id="+xfid+"&dj="+fdj})
                    // /zgmb
                    that.$router.push({path: "/zgmb"})
                }
            })
                })
                .catch(_ => {
                    this.$message({
                        message: '已取消',
                        type: 'warning'
                    });
                });
        },
        cancel(){
            this.$confirm('确认返回到上一页吗？')
            .then(_ => {
                this.$router.go(-1)
            })
            .catch(_ => {});
        },
        init(){
            this.restaurants = this.loadAll();
            console.log(this.$route.query)
            localStorage.setItem("id3",this.$route.query.id)
            this.edit.level = this.$route.query.level
            this.edit.id = this.$route.query.id
            if(this.edit.level == 1){
                this.title = '编辑诊改工作规划'
            }else if(this.edit.level == 2){
                this.title = '编辑专项工作规划'
            }else if(this.edit.level == 3){
                this.title = '编辑项目清单'
            }else if(this.edit.level == 4){
                this.title = '编辑工作任务'
            }
        }
    },
    mounted() {
        this.init()
        // this.restaurants = this.loadAll();
        // console.log(this.$route.query)
        // this.edit.level = this.$route.query.level
        // this.edit.id = this.$route.query.id
        // if(this.edit.level == 1){
        //     this.title = '编辑诊改工作规划'
        // }else if(this.edit.level == 2){
        //     this.title = '编辑专项工作规划'
        // }else if(this.edit.level == 3){
        //     this.title = '编辑项目清单'
        // }else if(this.edit.level == 4){
        //     this.title = '编辑工作任务'
        // }
        var zgzb=[];
        var fzyli=[];
        var that = this;
        var id = localStorage.getItem("id3")
        console.log(id)
        $.ajax({
            type: "post",
            url: this.baseUrl+"/ydjxzg/mb/goEditTarget.action",
            data: {
                targetid:id
            },
            dataType: "json",
            success: function (res) {
                console.log(res)
                if(res.khzb){
                        for(var i = 0;i < res.khzb.length;i++){
                            zgzb.push({id:res.khzb[i].id,value:res.khzb[i].zbmc})
                        }
                        console.log(zgzb)
                    }
                    if(res.userlist){
                        for(var i = 0;i < res.userlist.length;i++){
                            fzyli.push({id:res.userlist[i].user_id,value:res.userlist[i].name})
                        }
                        console.log(fzyli)
                    }
                that.edit.sj.mc = res.target.parent_name;
                that.edit.leader = res.target.parent_id == 0 ? '无': res.target.upManager;
                that.edit.mc = res.target.target_name;
                that.options = res.userlist
                console.log(that.options)
                that.edit.fzr = res.target.manager_name
                that.edit.fid=res.target.manager_id
                that.edit.jfys = res.target.funds
                that.edit.important = res.target.importancedj
                that.input = res.target.weight
                that.edit.start = res.target.start_time
                that.edit.end = res.target.end_time
                // 整合需要展示的参与人：
                var ccCyr = [];
                for (let q = 0; q < res.target.userEntity.length; q++) {
                    if (ccCyr.indexOf(res.target.userEntity[q].user_id) == -1) {
                        ccCyr.push(res.target.userEntity[q].user_id);
                    }
                    
                }
                that.edit.cyr = ccCyr //参与人
                console.log(that.edit.cyr, '参与人');
                that.edit.mark = res.target.target_stand_contion
                that.edit.zgzb = res.target.target_stand_name
                that.edit.zgzbID = res.target.target_stand
                that.edit.zgmb = res.target.target_stand_info
                that.edit.jj = res.target.target_info
                that.options101 = res.khzb
                that.edit.dj=res.target.target_dj
            }
        })
        this.restaurants = zgzb;
        this.restaurants1 = fzyli;
    },
    watch: {
        '$route':{
            handler(route){
                var _this = this
                console.log(route,_this)
                if(route.name == 'edit'){
                    _this.init()
                }
            },
            deep: true
        }
    }
}
</script>

<style>
#edit{
    width: 100%;
    height: 100%;
    margin-top: 20px;
}
#edit .el-row{
    padding: 0 10px;
}
#edit .el-row .el-card__body{
    padding: 50px 0;
}
#edit .title{
    width: 82%;
    height: auto;
    line-height: 40px;
    font-size: 24px;
    margin: 0 auto;
}
#edit .title .iconfont{
    margin-left: -16px;
}
#edit .el-form{
    padding: 0 8%;
}
#edit .el-form .sjgh{
    font-size: 14px;
    color: #606266;
    line-height: 40px;
}
#edit .el-form .sjgh .sj{
    display: flex;
}
#edit .el-form .sjgh .left{
    display: inline-block;
}
#edit .el-form .sjgh .right{
    display: inline-block;
    flex: 1;
    height: auto;
}
#edit .form_item{
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
@media all and (max-width: 1200px) {
    #edit .form_item .fzr,#edit .form_item .jfys,#edit .form_item .important,#edit .form_item .quanzhong{
        width: 21%;
    }
}
#edit .form_item .fzr,#edit .form_item .jfys,#edit .form_item .important,#edit .form_item .quanzhong{
    width: 23%;
}
#edit .form_item .starrttime,#edit .form_item .endtime{
    width: 45%;
}
#edit .form_item .el-date-editor{
    width: 100%;
}
#edit .el-form-item .el-form-item__label{
    display: block;
    float: none;
    text-align: left;
}
#edit .inline-input{
    width: 100%!important; 
}
#edit .zhengaimubiao{
    width: 100%;
    display: flex;
    justify-content: space-between;
}
#edit .khbz{
    width: 100%;
    display: flex;
    justify-content: space-between;
}
#edit .mb_mark{
    width: 49%;
    margin-right:15px;
}
#edit .mb_ipt1{
    width: 49%;
}
#edit .iconfont{
    font-weight: 900;
}
#edit .edit-footer{
    width: 84%;
    padding: 0 8%;
}
#addProject .edit-footer .el-button--default{
    background: #fdfdfd;
}
#edit .btn-custom-cancel {
    float: right;
    margin-left: 10px;
}
body .el-table th.gutter{
    display: table-cell!important;
}
input::-webkit-outer-spin-button,  
input::-webkit-inner-spin-button{  
    -webkit-appearance: none !important;  
    margin: 0;  
}
</style>


